//
// Print Overrides: Quick Links
//

// Horizontal
.quick-links-h__list-item {
  a:after {
    font-size: 1.3rem;
    margin-top:.5rem;
    flex: 1 1 auto;
    text-align: right;
  }
}
// Vertical
.quick-links-v__wrapper.col-start-fw {
  a{
    justify-content: unset;
    &:after {
      margin-left: .5rem;
      text-align: right;
      flex: 1 1 auto;
    }
  }
}
